@font-face {
  font-family: "Blogger Sans";
  src: url("../src/fonts/Blogger_Sans.otf");
}

@font-face {
  font-family: "Blogger Sans Bold";
  src: url("../src/fonts/Blogger_Sans_Bold.otf");
}

@font-face {
  font-family: "Aqum";
  src: url("../src/fonts/Aqum.otf");
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: rgb(233, 246, 224); */
  background: rgb(96, 230, 84);
  background: linear-gradient(
      180deg,
      rgba(96, 230, 84, 1) 0%,
      rgba(123, 238, 170, 1) 100%
    )
    no-repeat;
  background-attachment: fixed;
}

* {
  font-family: Blogger Sans;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
